.blog__section {
  background-color: $background-primary;
  margin: 0;
  min-height: 100vh;
  padding: 5rem 9%;
  text-align: center;
  width: 100%;
}

.blog__post-container {
  padding: 0rem;
}

.blog__btn {
  align-self: flex-end;
  border: 2px solid $primary;
  color: $primary;
  font-weight: 700;
  margin: 2rem auto 1rem;

  &:hover {
    background-color: $primary;
    color: #fff;
  }
}

.blog__post-card {
  background-color: #fff;
  display: grid;
  height: 550px;
  padding: 1.5rem 1rem;

  h3 {
    margin-bottom: 1.4rem;
  }

  .blog__btn {
    align-self: flex-end;
    border: 2px solid $primary;
    color: $primary;
    font-weight: 700;
    margin: 2rem auto 1rem;

    &:hover {
      background-color: $primary;
      color: #fff;
    }
  }
}

.blog__content,
.blog__content-middle {
  height: 100%;
  overflow: hidden;
  text-overflow: clip;
}

