.blog__post-date {
  color: lighten($color: $dark-gray, $amount: 40);
  font-weight: 500;
  justify-self: flex-start;
}

.blog__middle-card {
  background-color: $primary;
  color: #fff;

  p {
    color: #fff;
  }

  .blog__btn {
    align-self: flex-end;
    border: 2px solid #fff;
    color: #fff;
    font-weight: 700;
    margin: 2rem auto 1rem;

    &:hover {
      background-color: #fff;
      color: $primary;
    }
  }
}

.separator a img {
  width: 90%;
}
